<template>
  <div>
 <v-container fluid>     
    <v-alert v-model="alert" dismissible close-icon="mdi-delete" color="cyan" border="left" elevation="2" colored-border icon="mdi-twitter">
      {{mensaje}}
    </v-alert>
    <v-dialog  v-model="dialog" width="500" >
      <template v-slot:activator="{ on, attrs }">
          <v-btn color="purple lighten-1" class="fixedContainer"  dark v-bind="attrs" v-on="on" absolute right fab >
            <v-icon>mdi-plus</v-icon>
          </v-btn> 
      </template>

      <v-card class="spacing-playground pa-6">
        <v-card-title  class="headline" primary-title >
          Agregar Héroe
        </v-card-title>

          <form  >
              <v-text-field v-model="form.nombre" label="Nombre" required></v-text-field>
              <v-text-field v-model="form.descripcion"   label="Descripción" required></v-text-field>
              <v-file-input accept="image/png, image/jpeg, image/bmp" prepend-icon="mdi-camera" v-model="file" label="Imagen"></v-file-input>
              <v-file-input
                v-model="gallery" placeholder="Galeria" label="Galeria" multiple prepend-icon="mdi-paperclip" >
                <template v-slot:selection="{ text }">
                  <v-chip small label color="primary" >
                    {{ text }}
                  </v-chip>
                </template>
              </v-file-input>
            </form>
        <v-card-actions>
              <v-btn v-if="agregar" class="mr-4" @click="agregarNota()">Agregar</v-btn>
              <v-btn v-else class="mr-4" @click="editarNota(_id)">Editar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>         
    <v-row>
      <v-col cols="12">
        <v-row align="start"   class="grey lighten-5">
          <v-col v-for="(nota, index) in notas" :key="nota._id"  lg="cols-3" md="3" sm="6" >
          <v-card  class="ma-3 pa-6" sm="cols-6" md="cols-6">
            <v-img  class="white--text align-end" height="200px" :src="nota.imgUrl">
              <v-card-title>{{nota.nombre}}</v-card-title>
            </v-img>
              <v-card-subtitle class="pb-0">{{nota.descripcion}}</v-card-subtitle>
              <v-card-actions>
                <v-btn color="green" text   @click="setear(index)">
                  Editar<v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn color="red" text  @click="eliminarNota(nota._id)">
                  Eliminar<v-icon>mdi-delete</v-icon>
                </v-btn>
                <v-btn  icon  @click="nota.show = !nota.show" >
                  <v-icon>{{ nota.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                </v-btn>
              </v-card-actions>
                  <v-expand-transition>
                  <div v-show="nota.show">
                   <v-divider></v-divider>
                   <v-row align="center" class="spacer" no-gutters>
                      <v-col v-for="gallery in nota.gallery"  :key="gallery" cols="3" sm="3" md="3" >
                        <v-avatar  size="56px">
                            <v-img  class="white--text align-end"  :src="gallery"></v-img>
                        </v-avatar>
                      </v-col>
                    </v-row>   
                  </div>
                </v-expand-transition>
          </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        agregar:true,
        show: false,
        mensaje:'',
        dialog: false,
        alert:false,
        notas: [],
        agregar:true,
        _id:'',
        form:{       
          nombre:'',
          descripcion:'',
        },
        file:null,
        gallery:null
      }
    },
      created(){
    this.listarNotas();
  },
  methods:{
    listarNotas(){
      this.axios.get('nota')
      .then((response) => {
        this.notas = response.data;
      })
      .catch((e)=>{
        console.log('error' + e);
      })
    },
    setear(index){
      this._id=this.notas[index]._id;
      this.form.nombre=this.notas[index].nombre;
      this.form.descripcion=this.notas[index].descripcion;
      this.dialog=true;
      this.agregar=false;
    },
   async agregarNota(item){
      let data = new FormData();
      data.append('form',JSON.stringify(this.form));
      if (this.file) {
      data.append('filename',this.file);
     }
      if (this.gallery) {
        for( var i = 0; i < this.gallery.length; i++ ){         
          data.append('gallery',this.gallery[i]);
        } 
      }
         console.log(data);
     await  this.axios.post('nueva-nota', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
              }
          }).then(res => {
          this.form.nombre="";
          this.form.descripcion="";
          this.file=null;
          this.gallery=null;
          this.dialog=false;
          this.listarNotas();
        })
        .catch( e => {
          console.log(e.response.data.error.errors.nombre.message);

          // Alerta de mensaje
          this.showAlert();
          this.mensaje.color = 'danger';
          this.mensaje.texto = e.response.data.error.errors.nombre.message;
        })
      this.notas = {}
  },
        eliminarNota(id){
          this.$confirm("Estas seguro?").then(() => {
              this.axios.delete(`nota/${id}`)
                .then(res => {
                  let index = this.notas.findIndex( item => item._id === res.data._id )
                  this.notas.splice(index, 1);

                })
                .catch( e => {
                  console.log(e.response);
                })
                });
              },
               editarNota(_id){   
                 console.log(_id);          
              let datas = new FormData();
              datas.append('form',JSON.stringify(this.form));
              if (this.file) {
                datas.append('filename',this.file);
              }
              if (this.gallery) {
                for( var i = 0; i < this.gallery.length; i++ ){         
                  datas.append('gallery',this.gallery[i]);
                 }
              }
               this.axios.put(`nota/${_id}`,datas).then(res => {
                this.form.nombre="";
                this.form.descripcion="";
                this.file=null;
                this.gallery=null;
                this.dialog=false;
                this.listarNotas();
              })
              .catch(e => {
                console.log(e);
              })
            this.agregar = true;
          },
      }
  }
</script>
<style >
  .fixedContainer {

    position: fixed;
    padding: 2em;
    right: 0%;
    top: 90%;

}
</style>